.cafes-container {
    height: 100vh;
}

.cafes {
    background-color: black;
    display: flex;
    flex-wrap: wrap;
}

.cafe {
    background-color: rgb(232, 239, 232);
    margin: 10px;
    width: 500px;
    padding: 10px;
    border-radius: 15px;
}

@media screen and (max-width: 680px) {
    .cafes-container {
        height: 300px;
        padding: 20px;
    }
}